* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: rgb(236, 243, 241);
}

/* NAVBAR */

.nav-container {
  width: 100%;
  background-color: skyblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 30px;
  box-shadow: 0 1px 8px rgb(127 124 124 / 40%);
}

.nav-container img {
  width: 170px;
}
.nav-container ul {
  display: flex;
}
.nav-container ul li {
  margin-left: 12px;
  list-style: none;
  text-decoration: none;
}
.nav-container ul li a {
  text-decoration: none;
  color: black;
  letter-spacing: 2px;
}
@media screen and (min-width: 800px) {
  .nav-container {
    justify-content: space-around;
    padding: 20px 30px;
  }
  .nav-container ul li {
    margin-left: 30px;
  }
}

/* ABOUT US */

.about-us {
  max-width: 600px;
  margin: 0 auto;
  margin-top: 4rem;
}
.about-us h2 {
  text-align: center;
  letter-spacing: 3px;
}
.about-us p {
  color: rgb(31, 30, 30);
  letter-spacing: 2px;
  margin-top: 2rem;
  padding: 0px 30px;
  font-size: 18px;
  line-height: 30px;
}

/*ERROR PAGE*/

.error-page {
  margin-top: 3rem;
  text-align: center;
}
.error-page h2 {
  letter-spacing: 2px;
}
.error-page button {
  background-color: transparent;
  outline: none;
  cursor: pointer;
  border: none;
  margin-top: 1rem;
  text-transform: capitalize;
  letter-spacing: 2px;
  color: green;
  font-weight: bold;
  text-decoration: underline;
  transition: 0.3s all linear;
}
.error-page button:hover {
  text-decoration: none;
  color: black;
}

/* SEARCH FORM */

.search-form {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 3rem;
}
.form-search {
  width: 600px;
  max-width: 600px;
  background-color: white;
  margin: 0 30px;
  padding: 30px 40px;
  box-shadow: 0 5px 10px rgb(205 193 193 / 40%);
}

.search-form #search {
  display: block;
  margin: 0 auto;
  width: 100%;
  padding: 9px;
  margin-top: 1.5em;
  border: none;
  background-color: rgb(243, 243, 243);
}
.search-form label {
  color: rgb(1, 71, 1);
  letter-spacing: 3px;
  font-weight: bold;
}

/* ARTICLE COCKTAILS */
.cocktail .title h2 {
  text-align: center;
  margin-top: 2rem;
  letter-spacing: 3px;
  text-transform: capitalize;
  color: rgb(24, 23, 23);
}

.cocktail-list {
  display: grid;
  grid-template-columns: auto auto auto auto;
  place-items: center;
  margin: 0 0.5rem;
  margin-top: 2rem;
}

article {
  background-color: white;
  margin-top: 1rem;
  /* width: 300px; */
  max-width: 300px;
  margin: 1rem;
  border-radius: 2px;
  box-shadow: 0 5px 10px rgb(221 192 192 / 40%);
}

article img {
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
article .article-info {
  margin: 0.5rem 1rem;
}
.article-info a {
  text-decoration: none;
}
.article-info h2 {
  font-size: 25px;
  letter-spacing: 2px;
  color: rgb(20, 20, 20);
}
.article-info .glass {
  margin-top: 5px;
  letter-spacing: 3px;
  font-weight: 500;
  color: rgb(20, 20, 20);
}
.article-info .info {
  color: gray;
  margin-top: 5px;
  font-weight: 500;
  letter-spacing: 2px;
  font-size: 13px;
}

@media screen and (max-width: 1000px) {
  .cocktail-list {
    grid-template-columns: auto auto auto;
  }
}

@media screen and (max-width: 750px) {
  .cocktail-list {
    grid-template-columns: auto auto;
  }
}
@media screen and (max-width: 550px) {
  .cocktail-list {
    grid-template-columns: auto;
  }
}

/* SINGLE COCKTAIL */

.single-cocktail-container {
  display: flex;
  justify-content: center;
  margin-top: 4rem;
}

.single-cocktail-container .title h2 {
  text-align: center;
  font-size: 25px;
  letter-spacing: 2px;
  margin-bottom: 1em;
}

.s-cocktail {
  display: flex;
  align-items: center;
  max-width: 1050px;
  margin: 0 15px;
}
.s-cocktail img {
  width: 400px;
  height: 400px;
  border-radius: 3px;
  object-fit: cover;
}

.s-cocktail-info {
  margin-left: 2em;
  padding: 10px;
}

.s-cocktail-info p {
  margin-top: 1.5em;
  color: rgb(41, 41, 41);
  letter-spacing: 2px;
  font-weight: 500;
  line-height: 30px;
}
.s-cocktail-info .tag {
  background-color: rgb(174, 224, 174);
  color: rgb(4, 63, 4);
  padding: 5px;
  font-weight: 500;
  border-radius: 4px;
  margin-right: 10px;
}

@media screen and (max-width: 900px) {
  .s-cocktail {
    flex-direction: column;
    margin: 0 10px;
  }
  .s-cocktail-info {
    margin-left: 10px;
  }
  .s-cocktail img {
    max-width: 300px;
    height: 300px;
  }
}

/* LOADER */

.loading-section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 75vh;
}

.loader {
  width: 100px;
  height: 100px;
  border: 16px solid rgb(218, 216, 216);
  border-radius: 50%;
  border-top: 16px solid rgb(74, 197, 74);
  border-bottom: 16px solid rgb(75, 178, 247);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
